import Nav from "./Component/Nav";
import Intro from "./Component/Intro";
import Load from "./Component/Load";
import Source from "./Component/Source";
import ReactFullpage from "@fullpage/react-fullpage";
import { gsap } from "gsap";
import { useEffect } from "react";
import anime from "animejs/lib/anime.es.js";

const HomeAni = (tl, delay) => {
  tl.add(
    {
      targets: ".hi",
      opacity: [0, 1],
      translateY: ["20px", 0],
      duration: 1000,
    },
    delay
  )
    .add(
      {
        targets: ".name",
        opacity: [0, 1],
        translateY: ["20px", 0],
        duration: 1000,
      },
      "-=650"
    )
    .add(
      {
        targets: ".title",
        opacity: [0, 1],
        translateY: ["20px", 0],
        duration: 1000,
      },
      "-=650"
    );
  if (window.screen.width < 768) {
    tl.add(
      {
        targets: ".bo-con",
        opacity: [0, 1],
        translateY: ["25px", 0],
        duration: 1000,
      },
      "-=500"
    ).add(
      {
        targets: ".btn-work",
        opacity: [0, 1],
        translateY: ["-25px", 0],
        duration: 1000,
      },
      "-=1000"
    );
  } else {
    tl.add(
      {
        targets: ".bo-con",
        opacity: [0, 1],
        translateX: ["65px", 0],
        duration: 1000,
      },
      "-=500"
    );
    tl.add(
      {
        targets: ".btn-work",
        opacity: [0, 1],
        translateX: ["-65px", 0],
        duration: 1000,
      },
      "-=1000"
    );
  }
};

const Home = () => {
  useEffect(() => {
    var tl = anime.timeline({
      easing: "easeInOutSine",
      duration: 1500,
    });
    HomeAni(tl, 3900);
    tl.add(
      {
        targets: ".nav",
        opacity: [0, 1],
        translateY: ["-70px", 0],
        duration: 1000,
      },
      "5000"
    );
  });
  return (
    <>
      <Nav />
      <Source />
      <ReactFullpage
        //fullpage options
        scrollingSpeed={1000} /* Options here */
        onLeave={(destination) => {
          var section = anime.timeline({
            targets: ".section",
            easing: "easeInOutSine",
          });
          section
            .add({
              opacity: 0,
              duration: 300,
            })
            .add({
              opacity: 1,
              duration: 1000,
            });
          // gsap.to(".section", 0.3, { opacity: 0 });
          // gsap.to(".section", 2.25, { opacity: 1 }).delay(0.3);
          if (destination.index === 1) {
            var tl = anime.timeline({
              easing: "easeInOutSine",
              duration: 1500,
            });
            HomeAni(tl, "-=500");
          }
        }}
        render={({ state, fullpageApi }) => {
          return (
            <>
              <ReactFullpage.Wrapper>
                <div className="section" data-anchor="home">
                  <Load />
                  <Intro />
                </div>
                <div className="section" data-anchor="about">
                  <p>Section 2</p>
                </div>
                <div className="section" data-anchor="project">
                  <p>Section 3</p>
                </div>
                <div className="section" data-anchor="contact">
                  <p>Section 4</p>
                </div>
              </ReactFullpage.Wrapper>
            </>
          );
        }}
      />
    </>
  );
};

export default Home;
